/* 1. Top Bar & Header
=================================================*/

/* 1.1 Topbar
============================*/

.topbar {
  background: $white;
  border-bottom: $border-width $border-style $border-color;
  box-sizing: content-box;
  font-size: 13px;
  height: 50px;
  line-height: 50px;
  overflow: hidden;
  position: static;
  width: 100%;
  z-index: 200;

  a:active,
  a:hover,
  a:focus {
  }

  &.shrinked {
    border-bottom: $border-width $border-style $border-color;
    box-shadow: 0 1px 2px $lightest-grey-2;
    height: 63px;
    min-height: inherit;
    overflow: visible;
    padding-bottom: 0;
    position: fixed;
    width: 100%;
    z-index: 200;

    .social-icons {
      display: none;
    }
  }

  .social-icons {
    float: left;
    // margin: 0 0 0 21px;
    margin: -1.5px 0 0 21px;
    max-width: 500px;

    a {
      color: $black;
      // font-size: 12.5;
      font-size: 11.5px;
      margin-right: 9px;

      &:hover {
        color: $dark-grey;
      }
    }
  }

  .tools {
    float: right;
    margin: 0 20px 0 0;
    max-width: 220px;
  }
}

.topbar-fix {
  top: 0;
}

form#top-search {
  position: absolute;
  right: 0;
  z-index: 200;
}

input,
select,
textarea {
  font-size: 13px;
}

input[type='email'],
input[type='number'],
input[type='search'],
input[type='text'],
input[type='tel'],
input[type='url'],
input[type='password'],
textarea,
select {
  background-color: $white;
  border-color: $mid-grey-2;
  border-radius: 4px;
  border: $border-width $border-style $input--border-color;
  box-shadow: none;
  box-sizing: border-box;
  height: 38px;
  padding: 6px 10px;
}

button.search-button {
  background-color: transparent;
  border: 0;
  color: $black;
  display: inline-block;
  font-size: inherit;
  height: 27px;
  left: -30px;
  line-height: 1;
  overflow: hidden;
  padding: 0;
  position: relative;
  width: 27px;

  &::before {
    bottom: 14px;
    content: '\f002';
    font-family: fontAwesome;
    font-size: 14px;
    left: 6px;
    position: static;
  }
}

input.search-text {
  border-radius: 5% !important;
  display: inline-block;
  height: 25px !important;
  line-height: 1;
  width: 125px;
}

/* 1.2 Search Form
============================*/

.search-shrinked {
  display: none;
}

/* 1.3 Site Header
============================*/

.site-header {
  background: $white;
  height: auto;
  margin: 0 auto;
  max-height: 200px;
  min-height: 120px;
  padding: 10px 0 0;
  position: relative;
  transition: all .3s linear;
  width: 100%;
  z-index: 200;

  .header-shrinked {
    background: transparent;
    height: auto;
    left: 0;
    margin: 0 auto 50px;
    z-index: 400;
  }

}

.logo {
  height: 126px;
  margin: -50px auto 20px;
  text-align: center;
  width: 50%;

  a {
    color: $black;
    font: bold 6.4rem 'Voltaire', sans-serif;
    height: 80px;
    margin: -50px auto 0;
    max-width: 900px;
    min-width: 300px;
    overflow: hidden;
    text-decoration: none;
    text-transform: uppercase;
    width: auto;

    span {
      background: $white;
      padding: 0 30px;

      img {
        height: 80px;
      }
    }
  }

  .local-info {
    font-family: 'Roboto Slab', sans-serif;
    // font-family: sans-serif;
    height: 30px;
    // margin: auto;
    margin: 9px auto 0;
    max-width: 350px;
    min-width: 300px;
    text-align: center;
  }

  .local-date {
    font-size: 12px;
  }
}


.header-shrinked {

  .logo {
    height: 50px;
    left: 40px;
    margin: 0;
    position: fixed;
    width: 108px;

    a {
      height: 50px;
      line-height: 1;
      overflow: hidden;
      position: relative;
      top: -20px;

      span {
        background: transparent;
        height: 40px;
        padding: 0;

        img {
          height: 40px;
          position: relative;
          top: 20px;
        }
      }
    }
  }

  img.custom-logo {
    height: 50px;
    width: auto;
  }
}

.clearfix,
.row {
  &::before,
  &::after {
    content: '\0020';
    display: block;
    overflow: hidden;
    visibility: hidden;
    width: 0;
    height: 0;
  }
}

.wp-custom-logo .title-area {
  max-width: 216px;
}

.wp-custom-logo .site-container .title-area {
  max-width: initial;
  min-width: 216px;
  width: 216px;
}

img.custom-logo {
  top: -44px;
}

.date-shrinked {
  display: none;
}

