/* 2.2 Header Menu
============================*/

.nav-primary {
  border-color: $dark-grey;
  border-style: solid;
  border-width: 2px 0 1px;
  box-sizing: content-box;
  clear: both;
  height: 45px;
  line-height: 1.8;
  margin: 1px auto 0;
  max-width: 1200px;
  padding: 0;
  position: static;
  width: auto;

  a {
    padding: 0;

    &::after {
      content: '|';
      margin-left: 20px;
    }
  }

  ul {
    align-items: center;
    display: flex;
    justify-content: center;
    line-height: 1.4625;
    margin: 0;
    text-align: center;

    li {
      display: inline-block;
      line-height: 1.8;
      // padding: 8px 10px;
      padding: 8px 11.5px 8px 12.5px;
      position: relative;
      margin-bottom: 0;

      a {
        color: $black;
        font: 600 14px 'Source Sans Pro', sans-serif;
        // font: 600 14px sans-serif;
        text-transform: uppercase;
        transition: all .1s linear;

        &::after {
          color: $darkest-grey;
          font-family: 'PT Serif', 'times new roman', times, serif;
          font-size: 13px;

        }

        &:focus {
          outline: 2px solid $orange;
        }
      }

      &.current-menu-item a,
      a:hover,
      a:active,
      a:focus {
        color: $orange;
        text-decoration: none;
      }

      &:last-of-type a {
        background: $dark-grey;
        border: 5px solid $black;
        border-radius: 10%;
        color: $white;
        font-size: 14px;
      }
    }

    .sub-menu li {
      background: $black;
      border-bottom: 1px solid $black;
      padding: 10px 31px;
      width: 100%;
    }
  }

  li > ul a::after,
  ul li:last-of-type a::after {
    border-bottom: 0;
    content: '' !important;
    margin-left: 0 !important;
    width: 100%;
  }

  .sub-menu a::after {
    border-bottom: 1px solid $dark-grey;
    display: block;
    position: relative;
    top: 10.8px;
  }

  ul.sub-menu {
    li a,
    li:first-of-type a {
      background: $black;
      color: $lighter-grey;
      font-size: 1.2rem;
    }
  }
}

.genesis-nav-menu .sub-menu a,
.nav-primary .sub-menu a {
  background: $black;
}


