/* 3.1 Author Avatars - Voices
============================*/

.widget-wrap .author-avatars {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
  // width: 285px;
  width: 100%;

}

.widget-wrap .author-box {
  box-sizing: content-box;
  height: auto;
  margin: 0;
  width: 93px;

  a {
    color: $black;
    display: inline-block;
    width: 93px;

    &:hover {
      color: $orange;
    }
  }

  .avatar-box {
    height: 100px;
    margin: 0 0 1px;
    overflow: hidden;
    width: 95px;

    .avatar {
      border-radius: 0;
      float: none;
      height: auto;
      margin: 0;
      max-width: initial;
      width: 95px;
    }
  }

  .author-name {
    // font-size: 13px;
    font: 300 13px 'times new roman', times, serif;
    margin: 7px auto 10px;
    line-height: 1.1875;
    text-align: center;

  }

}

// max-width: 870px
@media only screen and (max-width: 54.375em) {
  section#text-18 {
    margin: 0 0 10px;
  }
}

