/* 4. Posts
=================================================*/

/* 4.1 Site Layout
============================*/

.content {
  float: left;
  margin: 0 10px;
  width: 880px;
}

.sidebar-content .content {
  float: right;
}

.full-width-content .content,
.landing-page .content {
  float: none;
  margin-left: auto;
  margin-right: auto;
}

.site-inner {
  margin: 0 auto;
  max-width: 1200px;
  // padding: 10px 0 0;
  padding: 30px 0 0;
}

/* 4.2 Posts Layout
============================*/

.post {
  border-bottom: $border-width $border-style $border-color;
  margin: 0 0 50px;
  padding: 0 0 20px;

  .thumbnail {
    height: auto;
    margin-bottom: 20px;
    max-height: 500px;
    overflow: hidden;
    position: relative;
    width: 100%;

    img,
    iframe {
      width: 100%;
      height: auto;
    }
  }

  .entry-tools a.morelink {
    float: right;
    margin-right: 0;
  }
}

.single .post .thumbnail {
  margin: 0 0 24px;
}

span.category {
  color: $mid-grey-2;
  display: block;
  font: 300 13px $base-font;
  line-height: 1.8em;
  margin: 20px auto -15px;
  text-align: center;
  width: 100%;
}

.title-divider {
  border-top: 2px dotted;
  display: block;
  margin: 0 auto 15px;
  max-width: 90px;
}

.entry-tools span,
.post .entry-tools a {
  color: $mid-grey-3;
  font-style: italic;
  margin-right: 20px;
}

.entry-title {
  line-height: 1.24;
  margin-top: 20px;
  text-align: center;
  text-transform: capitalize;
}

.entry-tools {
  font-size: 12px;
  margin: 10px auto 0;
}

/* 4.3 Author Bios
============================*/

.author-box {
  .avatar {
    height: 80px;
    width: 80px;
  }

  .author-name {
    color: $black;
    display: block;
    font: 700 18px $base-font;
    line-height: 1.111111;
    margin: 0 0 10px;
  }
}

.author-description {
  color: $mid-grey-1;
  font-size: 13px;
  font-weight: 300;
  line-height: 1.625;
  margin: 0 0 10px;
  margin-block-end: 10px;
}


/* 5. Archive Pages
=================================================*/

/* 5.1 Affiliate Links
============================*/

.affiliates-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.affiliate-item {
  align-items: center;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 0 6px;
}

.affiliate-link-text {
  font-size: 16px;
  line-height: 2;
}

.affiliate-img {
  margin: 0 0 4px;
}

/* 7. BackToTop Button
============================*/

#backToTop {
  background: $black;
  bottom: 30px;
  color: $white;
  display: none;
  float: right;
  font-size: 13px;
  height: auto;
  line-height: 1.8;
  padding: 5px 10px;
  position: fixed;
  right: 30px;
  width: auto;
  z-index: 100;
}

#backToTop.btt-shrinked {
  display: block;
}

